.container {
    margin: 0 auto;
    padding: 0 20px;
    width: 100%;
}

.section-about{
    background-color: $color-pink-background;
    background-image: url("../images/about/milkbgmobile@2x.png");
    padding-top: 125px;   
    padding-bottom: 39px;
    position: relative;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 70% 90%;

    @media (min-width: 768px) and (max-width: 1279px){
      padding-top: 130px;
      padding-bottom: 66px; 
      background-image: none; 
    }

    @media (min-width: 1280px){
       background-image: url("../images/about/milkbgdesktop.png"); 
       background-position: 70% 100%;
       padding-top: 125px;
       padding-bottom: 40px;
    }
    

    &__titleup{
        font-family: $font-head;
        color: $color-pink-2;
        font-weight: 400;
        font-size: 18px;
        margin-left: auto;
        margin-right: auto;
        font-style: normal;
        letter-spacing: 1.16;
        align-items: center;
        text-align: center;
        margin-bottom: 15px;
        margin-top: 125px;

    @media (min-width: 768px) and (max-width: 1279px) {
        margin-top: 130px;
        font-size: 26px;
        line-height: 1.15;    
    }
    @media (min-width: 1280px){
        font-size: 30px;
        line-height: 1.15; 
        margin-top: 125px;
    }
    }

    &__titledown{
        align-items: center;
        margin-bottom: 26px;
        padding-left: 9px;
        width: 200px;
        margin-left: auto;
        margin-right: auto;

    @media (min-width: 768px) and (max-width: 1279px) {
        font-size: 48px;
        line-height: 1.15;
        margin-bottom: 50px;
        width: auto;
       
    }

    @media (min-width: 1280px){
        font-size: 58px;
        line-height: 1.15; 
        letter-spacing: 0.06em;
        margin-top: 15px; 
        margin-bottom: 55px;
        text-align: center;
        display: inline;
        margin-left: 265px; 
        
    }
    }
&__wrapper{
    @media (min-width: 768px) and (max-width: 1279px){
        display: flex;
    }
    @media (min-width: 1280px){
        display: flex;
        margin-top: 55px;
    }
}
    &__image{
        width: 280px;
        height: 279px;
        margin-bottom: 46px;
        margin-left: auto;
        margin-right: auto;
        display: block;


    @media (min-width: 768px) and (max-width: 1279px){
        width: 340px;
        height: 339px;
    }
    @media (min-width: 1280px){
       
        margin-right: 74px;
        width: 516px;
        height: 516px; 
    }


    }
    &__desctription{
        padding: 0 30px; 
         
        
    @media (min-width: 768px) and (max-width: 1279px){
        display: block; 
        padding: 0;
        margin-left: 17px;
    }
    @media (min-width: 1280px){
        display: block;
        margin-top: 55px;
        
    }
    }

    &__one{
        font-family: $font-main;
        color: #000000;
        font-weight: 500;
        font-size: 12px;
        margin-left: auto;
        margin-right: auto;
        line-height: 19.44px;
        margin-bottom: 47px;
        display: block;

        @media (min-width: 768px) and (max-width: 1279px){
            font-size: 17px; 
            margin-bottom: 40px;
        }
        @media (min-width: 1280px){
            font-size: 16px; 
            margin-bottom: 35px;
            letter-spacing: 0.04em;
            line-height: 1.76;
            width: 463px;
        height: 79px; 

        }
    }

    &__two{
        font-family: $font-main;
        color: #907e82;
        font-weight: 500;
        font-size: 12px;
        margin-left: auto;
        margin-right: auto;
        line-height: 19.44px;
        margin-bottom: 15px;
        display: block;

        @media (min-width: 768px) and (max-width: 1279px){
            font-size: 17px; 
        }
        @media (min-width: 1280px){
            font-size: 14px; 
            letter-spacing: 0.04em;
            line-height: 1.94;
            width: 463px;
            height: 155px; 
            
        }
        }

    

    &__three{
        font-family: $font-main;
        color: #907e82;
        font-weight: 500;
        font-size: 12px;
        margin-left: auto;
        margin-right: auto;
        line-height: 19.44px;
        margin-bottom: 35px;
        display: block;

        @media (min-width: 768px) and (max-width: 1279px){
            font-size: 17px; 
            margin-bottom: 62px;
        }
        @media (min-width: 1280px){
            font-size: 14px; 
            margin-bottom: 45px;
            letter-spacing: 0.04em;
            line-height: 1.94; 
        }
        }

    &__btn{
        width: 176px;
        height: 44px;
        font-weight: 700;
        position: relative;
        display: block;
        text-align: center;
        cursor: pointer;
        margin-left: 50px;
        margin-right: 94px;
        

        @media (min-width: 768px) and (max-width: 1279px){
            font-size: 16px; 
            margin-left: 0px;
        }

        @media (min-width: 1280px){
            font-size: 16px; 
            margin-left: 0px; 
        }
    }

    &__icon{
        fill: $color-white;
        margin-left: 13px;
    }


}